<template>
  <div id="index">
    <dv-full-screen-container class="bg">
      <dv-loading v-if="loading">Loading...</dv-loading>
      <div class="header-box">
        <div class="time-box">
          <div class="time">{{ time }}</div>
          <div style="margin-left: 1rem">
            <div class="date">{{ date }}</div>
            <div class="date">{{ week }}</div>
          </div>
        </div>
        <div class="title-box">
          <span>{{ title }}</span>
        </div>
      </div>
      <div class="content-box">
        <div style="display: inline-block">
          <div
            style="display: inline-block"
            @click="$router.push('/renyuanguanli')"
          >
            <img class="item" src="/static/1.png" />
          </div>
          <div
            style="display: inline-block"
            @click="$router.push('/zhilifuwu')"
          >
            <img class="item" src="/static/2.png" />
          </div>
          <div
            style="display: inline-block"
            @click="$router.push('/gongtongfuyu')"
          >
            <img class="item" src="/static/3.png" />
          </div>
        </div>
      </div>
    </dv-full-screen-container>
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      loading: true,
      time: "",
      date: "",
      week: "",
      title: "数字白马·基层单元智治中心",
    };
  },
  mounted() {
    this.cancelLoading();
    setInterval(this.getTime, 1000);
  },
  methods: {
    cancelLoading() {
      setTimeout(() => {
        this.loading = false;
      }, 500);
    },
    getTime() {
      var date = new Date();
      var YY = date.getFullYear();
      var MM =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var DD = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      var hh = date.getHours() < 10 ? "0" + date.getHours() : date.getHours();
      var mm =
        date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes();
      var ss =
        date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
      var myddy = date.getDay(); //获取存储当前日期
      var weekday = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ];
      this.time = hh + ":" + mm + ":" + ss;

      this.date = YY + "年" + MM + "月" + DD + "号";
      this.week = weekday[myddy];
    },
  },
};
</script>

<style lang="scss">
@import "../assets/style/index.scss";
.header-box {
  width: 100%;
  height: 15vh;
  .time-box {
    width: 15%;
    height: 5vh;
    position: absolute;
    top: 5rem;
    left: 3rem;
    > div {
      display: inline-block;
    }
    .time {
      color: #64e5ce;
      font-size: 28px;
      margin-left: 0.2rem;
    }
    .date {
      color: #daf4ef;
      font-size: 12px;
    }
  }
  .title-box {
    position: fixed;
    top: 2.5rem;
    left: 39%;
    span {
      color: #ffffff;
      font-size: 40px;
      font-weight: bold;
      display: flex;
    }
  }
}
.content-box {
  margin-top: 235px;
  margin-left: 220px;
  height: 100%;
  padding: 10px 30px 0 30px;
}
.item {
  padding-left: 200px;
}
</style>



